export default "<template>\r\n  <nm-box>\r\n    <pre>\r\n      《虞美人》\r\n\r\n           唐·李煜\r\n\r\n      春花秋月何时了，往事知多少。\r\n\r\n      小楼昨夜又东风，\r\n\r\n      故国不堪回首月明中。\r\n\r\n      雕栏玉砌应犹在，只是朱颜改。\r\n\r\n      问君能有几多愁，\r\n\r\n      恰似一江春水向东流。\r\n\r\n      风回小院庭芜绿，柳眼春相续。\r\n\r\n      凭阑半日独无言，\r\n\r\n      依旧竹声新月似当年。\r\n\r\n      笙歌未散尊前在，池面冰初解。\r\n\r\n      烛明香暗画楼深，\r\n\r\n      满鬓清霜残雪思难任。\r\n    </pre>\r\n  </nm-box>\r\n</template>\r\n"